@import "variables";

@mixin semantic--headings() {
  h1, h2, h3, h4 {
    color: $jk-green;
  }
  h1 {
    font-size: 4rem;
    margin: 2rem 6rem 3rem;
    @include mobile() {
      font-size: 10vw;
      margin: 1.5rem 2rem 2rem;
    }
    @include tablet() {
      font-size: 8vw;
      margin: 2.1rem 3rem 3rem;
    }
  }
}

@mixin flex-center-center {
  display: flex;
  align-content: center;
  justify-content: center;
  flex-wrap: wrap;
}

@mixin flex-colum {
  display: flex;
  flex-direction: column;
}

@mixin jk-button {
  font-size: 1.25rem;
  padding: 0.5rem 2rem;
  margin: 5px;
  border-radius: 10px;
  box-shadow: $jk-shadow;
  &:hover {
    transform: $transform-scale;
  }

}

@mixin mattiSize {
  img {
    max-height: $matti-height;
  }
}

@mixin teppoSize {
  img {
    max-height: $teppo-height;
  }
}

@mixin mattiMobileSize {
  img {
    max-height: $matti-height--mobile;
  }
}

@mixin teppoMobileSize {
  img {
    max-height: $teppo-height--mobile;
  }
}

@mixin mattiTabletSize {
  img {
    max-height: $matti-height--tablet;
  }
}

@mixin teppoTabletSize {
  img {
    max-height: $teppo-height--tablet;
  }
}